import React, { useState } from 'react';
import Page1 from '../Homepage/Home';
import Page2 from '../ContactPage/ContactPage';
import Page3 from '../PotfolioPage/Potfolio';
import Page4 from '../ProjectPage/Project';
import Page5 from '../BlogPage/Blog';
  
const Nav = () => {
  const [currentPage, setCurrentPage] = useState('Page1');

  const goToHomePage = () => {
    setCurrentPage('Page1');
  };

  const goContactPage = () => {
    setCurrentPage('Page2');
  };

  const goPotfolioPage = () => {
    setCurrentPage('Page3');
  };
  const goProjectPage = () => {
    setCurrentPage('Page4');
  };
  const goBlogPage = () => {
    setCurrentPage('Page5');
  };



  const renderPage = () => {
    switch (currentPage) {
      case 'Page1':
        return <Page1 topPage3={goPotfolioPage} topage4={goProjectPage} topage5={goBlogPage}/>;
      case 'Page2':
        return <Page2/>;
      case 'Page3':
        return <Page3/>;
      case 'Page4':
        return <Page4/>;
      case 'Page5':
        return <Page5/>;
      default:
        return null;
    }
  };
  return (
    <div>
      <nav>
        <div className="nav-wrapper cyan darken-4">
          <ul id="nav-mobile" className="right hide-on-med-and-down">
              <li><a href={goToHomePage} onClick={goToHomePage}>Home</a></li>
              <li><a href={goPotfolioPage} onClick={goPotfolioPage}>Portfolio</a></li>
              <li><a href={goProjectPage} onClick={goProjectPage}>Projects</a></li>
              <li><a href={goBlogPage} onClick={goBlogPage}>Blog</a></li>
              <li><a href={goContactPage} onClick={goContactPage}>Contact</a></li>
          </ul>
          <ul id="nav-mobile" className="left hide-on-med-and-up show-on-med-and-down">
              <li><a href={goToHomePage} onClick={goToHomePage}>Home</a></li>
              <li><a href={goPotfolioPage} onClick={goPotfolioPage}>Portfolio</a></li>
              <li><a href={goProjectPage} onClick={goProjectPage}>Projects</a></li>
              <li><a href={goBlogPage} onClick={goBlogPage}>Blog</a></li>
              <li><a href={goContactPage} onClick={goContactPage}>Contact</a></li>
          </ul>
       
        </div>
      </nav>
      {renderPage()}
    </div>
  );
};

export default Nav;
