import React from 'react';
function Topcard(props){

    const cardStyle ={
        margin:"5%"
    }
    return (
        <center>
                <div className="row offset-l4" style={cardStyle}>
                
                <div class="col s6 m6 l2">
                    <div class="row"><span class="card-title">{props.title}</span>
                        <div class="col s12 m7 l12">
                            <a href={props.link} onClick={props.link}>
                                <div class="card">
                                    <div class="card-image">
                                    <img src={props.avater} alt=''></img>
                                    </div>
                                    <div class="card-content">
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>

                <div class="col s6 m6 l2">
                    <div class="row"><span class="card-title">{props.title1}</span>
                        <div class="col s12 m7 l12">
                            <a href={props.link1} onClick={props.link1}>
                                <div class="card">
                                    <div class="card-image">
                                    <img src={props.avater1} alt=''></img>
                                    </div>
                                    <div class="card-content">
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>

                <div class="col s6 m6 l2">
                    <div class="row"><span class="card-title">{props.title2}</span>
                        <div class="col s12 m7 l12">
                            <a href={props.link2} onClick={props.link2}>
                                <div class="card">
                                    <div class="card-image">
                                    <img src={props.avater2} alt=''></img>
                                    </div>
                                    <div class="card-content">
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>                                

                </div>
        </center>
 

        );
    }    
export default Topcard;