import React from 'react';
import business_image from '../../assets/images/image_6.png';
import business_image1 from '../../assets/images/image_4.png';
import business_image2 from '../../assets/images/image_7.png';
import WebConfig from '../Main/WebConfig';
import Topcard from './Topcard';


const HomePage = () => {
  return (
    <div>
              <>
              <div class="col s12 m6">
                  <div class="card white darken-1">
                    <h3><center>{WebConfig.webname} Projects</center></h3> 
                    <Topcard 
                    avater={business_image} title={"PIAPI"} link={"https://petermeshack.github.io/piapi.github.io/"}
                    avater1={business_image1} title1={"CONSOLTANTS"} link1={"https://petermeshack.github.io/lanceconsoltant.github.io/"}
                    avater2={business_image2} title2={"GAMES"} link2={"https://petermeshack.github.io/"}
                    />
                </div>
            </div>
        </>

    </div>
  );
};

export default HomePage;