import React from 'react';
import 'materialize-css/dist/css/materialize.css';
import Header from '../Navigator/Header';
import Footer from '../Navigator/Footer';
import WebConfig from './WebConfig';
//import PageUpButton from './PageUpButton';
const Main=()=>{
    return(
        <div>
            <Header/>
            <Footer webname={WebConfig.webname} address={WebConfig.address} phone={WebConfig.phone}/>
            {/*<PageUpButton />*/}
        </div>

    );
};
export default Main;