import React from 'react';
const FooterPage = (props) => {
    const date = new Date();

  return (
      <footer class="page-footer cyan darken-4">
        <div class="container">
        <div class="row">
                <div class="col l6 s12">
                    <h5 class="white-text">{props.webname}.com</h5>
                    <p class="grey-text text-lighten-4">
                        Address<br></br>
                        ..........<br></br>
                        {props.address} Kenya
                    </p>
                    <p class="grey-text text-lighten-4">
                        Phone<br></br>
                        ..........<br></br>
                        {props.phone}
                    </p>
                </div>
                <div class="col l4 offset-l2 s12">
                    <p>

                        <b>About me<br></br></b>
                        Sofware Developer, creating a wide range  of sotware, current location found in {props.address}<br></br>

                    </p>
                </div>
        </div>
        </div>
        <div class="footer-copyright">
        <div class="container">
        <p>Copyright &copy; 2023-" . {date.toLocaleDateString()} . " {props.webname}.com</p>
        </div>
        </div>     
        </footer>
  );
};


export default FooterPage;