import Gmail from "../../assets/images/gmail.png";
import Facebook from "../../assets/images/facebook.png";
import Whatsapp from "../../assets/images/whatsapp.png";
import Linkedin from "../../assets/images/linkedin.png";
import ShowImage from "./ShowImage";
import WebConfig from "../Main/WebConfig";

function ContactSocialMedia(props){
    const formstyle={
        width:"90%",
        margin:"3%"
    }
    return (
        <>
        <center style={formstyle}>
            <div class="row offset-l4">
             <span class="card-title">{props.title}</span>
                    <div class="col s12">
                            <a href={WebConfig.gmail} >
                            <ShowImage image_path={Gmail} width={"5%"} height={"5%"}/>
                            </a>
                            <a href={WebConfig.facebook} >
                            <ShowImage image_path={Facebook} width={"5%"} height={"5%"}/>
                            </a>
                            <a href={WebConfig.linkedin} >
                            <ShowImage image_path={Linkedin} width={"5%"} height={"5%"}/>
                            </a>
                            <a href={WebConfig.whatsapp} >
                            <ShowImage image_path={Whatsapp} width={"5%"} height={"5%"}/>
                            </a>
                    </div>

        </div>
        </center>
        </>
        );
    }    
export default ContactSocialMedia;