import React, { useEffect, useState } from 'react';
import jsonData from './database.json';

const BlogReader = () => {
  const [blogData, setBlogData] = useState([]);
  const targetField = Object.keys(jsonData.WebDatabase.BlogTable)[0]; // Get the first field name
  const targetIds = extractIds(jsonData.WebDatabase.BlogTable[targetField]); // Extract IDs from the first field

  function extractIds(fieldData) {
    return fieldData.map(item => item.id);
  }

  useEffect(() => {
    const filteredData = getFilteredData(jsonData.WebDatabase.BlogTable, targetIds);
    setBlogData(filteredData);
  }, []);

  const getFilteredData = (data, targetIds) => {
    return targetIds.map(id => {
      const filteredFields = {};
      for (let key in data) {
        filteredFields[key] = data[key].filter(item => item.id === id).map(item => item.content);
      }
      return filteredFields;
    });
  };

  if (blogData.length === 0) {
    return <div>Loading...</div>;
  }

  const removeImagesPrefix = imagePath => {
    const prefix = '/images/';
    if (imagePath.indexOf(prefix) === 0) {
      return imagePath.substring(prefix.length);
    }
    return imagePath;
  };

  return (
    <>   
    {blogData.map((blog, index) => (
        
    <div className="col s12 m6 l12" key={index}>
        <div class="row"><span class="card-title">{blog.title}</span>
            <div class="col s12 m7 l12">    
                <div class="card">
                        <div class="card-image">
                            <img src={require(`${removeImagesPrefix(blog.imagepath)}`).default} alt="Blog" />
                        </div>
                        <div class="card-content">
                            <p>{blog.subtitle}</p>
                        </div>
                        <p>Date: {blog.date}</p>
                </div>
            </div>
        </div>
    </div>
    ))}
    </>



  );
};

export default BlogReader;
