function Middlecard(props){
    const imageStyle ={
        width:"50%",
        height:"30%",
        margin:"1%",
    }
    const imageStyle1 ={
        width:"60%",
        height:"30%",
        margin:"1%",
    }
    return (
        <div className="card-content black-text">
            <div class="col s6 m6">
                <center> 
                <span class="card-title">{props.title}</span>
                <img src={props.avater_path} style={imageStyle} className="hide-on-med-and-down" alt="None"></img><br></br>
                <img src={props.avater_path} style={imageStyle1} className="hide-on-med-and-up show-on-med-and-down" alt="None"></img><br></br>                </center>
                <center>
                <p>{props.subtitle}</p>
                <button class="btn waves-light" onClick={props.buttonlink} type="submit" name="action">{props.buttonName}</button>
                </center>
            </div>
        </div>

        );
    }    
export default Middlecard;