import React from 'react';
//import WebConfig from '../Main/WebConfig';
import BlogReader from './BlogReader';
function Topcard(props){

    const cardStyle ={
        margin:"5%"
    }
    return (
        <center>
                <div className="row offset-l4" style={cardStyle}>
                    <BlogReader/>
                
                </div>
        </center>
 

        );
    }    
export default Topcard;